<template>
  <div class="course">
    <div class="header" :class="config? 'fixedClass':'' ">
      <van-nav-bar left-arrow @click-left="onClickLeft">
        <template #title>
          <div v-show="!config">课程详情</div>
          <div v-show="config">
            <span @click="toKcjs">课程介绍</span>
            <span @click="toKcdg">课程大纲</span>
            <span @click="toKcpj">课程评价</span>
          </div>
        </template>
        <template #right>
          <van-cell @click="showPopup">
            <van-icon name="share" size="25" color="black" />
          </van-cell>
        </template>
      </van-nav-bar>
    </div>
    <van-popup v-model="show">
      <p>分享</p>
      <qriously :value="initQCode" :size="138" />
    </van-popup>

    <div class="top">
      <span>
        <van-icon name="star-o" v-show="xian" @click="shou" />
        <van-icon name="star" v-show="!xian" color="rgb(255, 102, 0)" @click="shou" />
      </span>
      <h3>{{ arr.title }}</h3>
      <p class="p1">{{ arr.price | Myprice }}</p>
      <p class="p2">共{{ arr.is_free }}课时| 共有{{ arr.sales_num }}报名</p>
    </div>
    <!-- 教师团队 -->
    <div class="tea" v-for="(m,n) of fl" :key="n">
      <h3>教师团队</h3>

      <div class="small">
        <img :src="m.teacher_avatar" alt />
      </div>
      <span>{{m.teacher_name}}</span>
    </div>
    <!-- 课程介绍 -->
    <div class="introduce">
      <h3 ref="kcjs">课程介绍</h3>
      <p v-html=" arr.course_details "></p>
    </div>
    <!-- 教学大纲 -->
    <div class="dg">
      <h3 ref="kcdg">教学大纲</h3>
      <p v-html=" arr.course_details "></p>
    </div>
    <div class="pl">
      <h3 ref="kcpj">课程评论</h3>
      <ul>
        <li v-for="(item,index) in nav" :key="index">
          <div class="small">
            <img :src="item.avatar" alt />
          </div>
          <div class="right">
            <p>
              <span>{{ item.nickname }}</span>
              <van-rate color="orange" v-model="value" />
            </p>
            <h3>{{ item.content }}</h3>
          </div>
        </li>
      </ul>
    </div>
    <van-button color="#eb6100" block @click="los">{{ lv | bt}}</van-button>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      config: false,
      arr: [],
      fl: [],
      nav: [],
      xian: true, // 收藏
      value: 3,
      initQCode: "https://wap.365msmk.com/course-detail?id=100&courseType=2",
      show: false,
      lv: ""
    };
  },
  mounted() {
    this.xq();
    this.ql();

    //  this.$axios.get("https://www.365msmk.com/api/app/courseComment").then((msg)=>{
    //    console.log(msg)
    //  })
    //  获取滚动条数据
    window.addEventListener("scroll", this.handleScrollx, true);
  },
  methods: {
    //  二维码生成
    showPopup() {
      this.show = true;
    },
    // 返回上个页面
    onClickLeft() {
      this.$router.go(-1);
    },
    toKcdg() {
      this.$refs.kcdg.scrollIntoView();
    },
    toKcpj() {
      this.$refs.kcjs.scrollIntoView();
    },
    toKcjs() {
      this.$refs.kcjs.scrollIntoView();
    },
    // 详情
    async xq() {
      let { data: res } = await this.$http.get(
        `/api/app/courseInfo/basis_id=${this.$route.query.id}&course_type=${this.$route.query.course_type}`
      );
      //   console.log(this.$route.query.id,this.$route.query.course_type)
       console.log(res)
      this.arr = res.data.info;
      this.lv = this.arr.is_join_study
      this.fl = res.data.teachers;
    },
    // 获取评论
    async ql() {
      let { data: res } = await axios.post(
        `/api/app/courseComment?id=${this.$route.query.id}&limit=10&page=1`
      );
      // console.log(res)
      this.nav = res.data.list;
    },
    handleScrollx() {
      //  console.log("滚动高度",window.pageYOffset)
      this.config = window.pageYOffset > 50;
    },
    //课程收藏
    shou() {
      if (this.xian) {
        this.xian = !this.xian;
        this.$http
          .post("api/app/collect", {
            course_basis_id: this.courseInfo.id,
            type: 1
          })
          .then(res => {
            // console.log(res)
            Toast.success(res.msg);
          });
      } else {
        this.xian = !this.xian;
        Toast.success("取消收藏");
      }
    },
    // 切换
    async los() {
      let { data: res } = await this.$http.post(
        `/api/app/order/downOrder?shop_id=${this.arr.id}&type=${this.arr.course_type}`
      );
      console.log(res)
      this.xq()
    }
  },
  created() {
    //  初始化回到顶部显示页面
    document.body.scrollTop = document.documentElement.scrollTop = 0;
  },
  filters: {
    Myprice(val) {
      if (val == 0) {
        return "免费";
      } else {
        return `￥${(val / 100).toFixed(2)}`;
      }
    },
    // 过滤立即预约和立即学习
    bt(val) {
      if (val == 0) {
        return "立即预约";
      } else if (val == 1) {
        return "立即学习";
      }
    }
  }
};
</script>

<style lang="scss">
.course {
  .van-tabs--line .van-tabs__wrap {
    height: 105px;
  }
  .van-tabs__content {
    padding: 0 40px;
  }
  .van-tab__text--ellipsis {
    font-size: 30px;
    color: #8c8c8c;
  }
  .van-tabs {
    background: #fff;
  }
  .van-nav-bar {
    height: 100px;
    background: rgba(0, 0, 0, 0.01);
  }
  .van-icon-arrow-left::before {
    color: black;
    font-size: 45px;
  }
  .van-nav-bar__title {
    font-size: 35px;
    color: black;
  }
  .header {
    width: 100%;
    height: 100px;
  }
  // 脱离文档流样式
  .fixedClass {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 999;
  }
  .van-cell {
    background-color: #edeff2;
  }
  .top {
    width: 95%;
    height: 200px;
    background-color: white;
    padding: 20px;

    h3 {
      font-size: 30px;
      line-height: 46px;
    }
    .p1 {
      color: orange;
      font-size: 30px;
    }
    .van-icon {
      float: right;
      margin: 10px 20px;
    }
    .p2 {
      line-height: 60px;
      font-size: 26px;
      color: #8c8c8c;
    }
  }
  .tea {
    margin-top: 30px;
    width: 95%;
    height: 200px;
    background-color: white;
    padding: 20px;
    h3 {
      font-size: 30px;
    }
    .small {
      width: 80px;
      height: 80px;
      border: 1px solid #ccc;
      border-radius: 50%;
      margin-top: 10px;
      margin-left: 10px;
      overflow: hidden;
      img {
        width: 100%;
        height: 100%;
      }
    }
    span {
      color: #8c8c8c;
      font-size: 24px;
      margin-left: 20px;
      margin-top: 10px;
    }
  }
  .introduce {
    height: 170px;
    width: 95%;
    background-color: white;
    margin-top: 20px;
    padding: 10px 20px;
    h3 {
      font-size: 30px;
      margin-bottom: 20px;
    }
    p {
      font-size: 28px;
    }
  }
  .dg {
    height: 130px;
    width: 95%;
    background-color: white;
    margin-top: 20px;
    padding: 10px 20px;
    h3 {
      font-size: 30px;
      margin-bottom: 20px;
    }
    p {
      font-size: 26px;
      line-height: 50px;
    }
  }
  .pl {
    height: 250px;
    width: 90%;
    background: white;
    padding: 20px 20px;
    margin-top: 20px;
    margin-bottom: 160px;

    h3 {
      font-size: 30px;
    }
    ul {
      width: 80%;
      li {
        height: 120px;
        display: flex;
        .small {
          width: 80px;
          height: 80px;
          border: 1px solid #ccc;
          border-radius: 50%;
          margin-top: 10px;
          margin-left: 10px;
          overflow: hidden;
          img {
            width: 100%;
            height: 100%;
          }
        }
        .right {
          width: 100%;
          //   border: 1px solid red;
          margin-left: 20px;
          p {
            font-size: 26px;
            line-height: 40px;
            .van-rate {
              margin-left: 10px;
            }
          }
          h3 {
            font-size: 24px;
            color: #8c8c8c;
          }
        }
      }
    }
  }
  .van-button {
    width: 100%;
    height: 90px;
    position: fixed;
    bottom: 0;
  }
}
</style>

